.movies {
  display: grid;
  grid-template-columns: auto auto;
  margin-top: 3%;
  grid-gap: 100px;
  justify-content: center;
  align-items: center;
}

.nav {
  position: fixed;
  top: 0;
  height: 5vh;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: auto;
    width: 100%;
  }
}
