a {
  color: #fff;
  text-decoration: none;
}
img {
  display: block;
}
.title {
  text-decoration: none;
  color: #fff;
  font-size: 40px;
  font-weight: 900;
}
.description {
  color: #fff;
}
.coverImg {
  width: 230px;
  height: 345px;
  margin: 15px;
  transition: 0.8s;
  border-radius: 20px;
}
.coverImg:hover {
  transform: scale(1.1);
}
.backgroundImg {
  width: 80%;
}
.genres {
  color: #fff;
  list-style: none;
  margin-right: 10px;
}
.info {
  margin-right: 10px;
  font-weight: 700;
}
.movieBox {
  box-sizing: border-box;
  padding: 30px;
  border: 3px rgba(255, 255, 255, 0.7) solid;
  border-radius: 10px;
  max-width: 700px;
}
